<template>
  <div class="detail" v-if='empety'>
    <div class="swiper">
      <mt-swipe :auto="4000">
        <mt-swipe-item class="lunbotu"><img :src="list.img"></mt-swipe-item>
        <mt-swipe-item class="lunbotu"><img :src="list.Img"></mt-swipe-item>
        <mt-swipe-item class="lunbotu"><img :src="list.iMg"></mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="det">
      <h5>{{list.title}}（0.5gx10片x2板/盒）（片剂）</h5>
      <span class="price">￥{{list.price.toFixed(2)}}</span>
      <p class="wenhao">批准文号：国药准字{{list.pizhun}}</p>
      <p class="company">生产企业：{{list.qiye}}有限公司</p>
      <p class="bing">慢性胃炎：{{list.jieshao}}等</p>
    </div>
      <div class="count">数量：
        <div class="unIncrease">-</div>
        <div class="num"><input type="text" value='1'></div>
        <div class="increase">+</div>
      </div>
    <div class="addToCarts">
      <mt-button type="primary" @click="addToCart({
        id: id,
        title: list.title,
        url: list.img,
        price: list.price,
        yunfei: list.yunfei,
        yaofang: list.yaofang
      })">加入购物车</mt-button>
    </div>
  </div>
</template>

<script>
import { getdetail } from '@/service.js'
import { mapMutations } from 'vuex'
export default {
  name: 'detail',
  data () {
    return {
      list: {},
      empety: false,
      id: Number(this.$route.params.aid)
    }
  },
  mounted () {
    getdetail().then(res => {
      if (res.data.code === '200') {
        this.list = res.data.data
        this.empety = true
      }
    })
  },
  methods: {
    ...mapMutations(['addToCart'])
  }
}
</script>

<style lang='scss' scoped>
  .detail {
    height: 100%;
    .swiper {
      height: 40%;
      border-bottom: 1px solid #999;
      .lunbotu{
        width: 100%;
        position: relative;
        img {
          width: 100%;
          height: 100%;
          position: absolute;
          top:0;
          left: 0;
        }
      }
    }
    .det {
      padding: 15px 10px;
      line-height: 25px;
      h5 {
        font-size: 16px;
        font-weight: normal;
      }
      .price {
        color:red;
        font-weight: 600;
      }
      .wenhao, .company, .bing {
        font-size: 15px;
        color: rgb(100, 95, 95);
      }
    }
    .count {
      float: left;
      width: 127px;
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      .unIncrease, .increase{
        font-size: 20px;
        text-align: center;
        line-height: 20px;
        border:1px solid #999;
        float: left;
      }
      .unIncrease {
        width: 20px;
        height: 20px;
      }
      .increase {
        width: 20px;
        height: 20px;
      }
      .num {
        width: 30px;
        height: 20px;
        border:1px solid #999;
        border-left: 0;
        border-right: 0;
        float: left;
        input {
          width: 30px;
          border: none;
          line-height: 20px;
          text-align: center;
          display: block;
        }
      }
    }
    .addToCarts {
      float: left;
      margin-left: 50px;
    }
  }
</style>



